@import url(https://fonts.googleapis.com/css2?family=Orbitron&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Pangolin&display=swap);
html{font-size:16px;font-family:'Orbitron', sans-serif}*{box-sizing:border-box;margin:0;padding:0}#app{height:100vh;width:100vw;background-image:url(/JSCalculator/static/media/bg2.3299b029.jpg);background-size:cover}#app #drop{position:absolute;z-index:5;top:0;left:0;background-color:gray;-webkit-filter:opacity(50%);filter:opacity(50%);height:100%;width:100%}#app #core{position:relative}#app #core #calc{-webkit-user-select:none;-ms-user-select:none;user-select:none;height:500px;width:400px;background-color:#000;border:#3b3636 3px solid;border-radius:5px}#calcPad{position:relative;margin:10px;height:380px;width:380px;display:grid;grid-template:repeat(5, 1fr)/repeat(5, 1fr);grid-gap:5px}#calcPad #equals{grid-area:4/5/6/6}#calcPad .int{transition:all 50ms ease-in-out;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;cursor:pointer;border-radius:2px}#calcPad .num{background:linear-gradient(45deg, #252525, #414141);color:#a3a3a3}#calcPad .dang{background:linear-gradient(45deg, #252525, #414141);color:#c53f3f}#calcPad .oper{background:linear-gradient(45deg, #252525, #414141);color:#c5ba54}#calcPad .enter{background:linear-gradient(45deg, #252525, #414141);color:#7f70c2}#scr{display:block;position:relative;background-color:black;color:white;width:380px;height:90px;margin:5px auto}#scr #display{display:block;position:absolute;bottom:0;right:0;text-align:right;font-size:2rem}#histList{line-height:1.2rem;font-family:'Pangolin', cursive;position:relative;z-index:11;width:400px;overflow:hidden;background-color:#313131;color:#a3a3a3;background-image:linear-gradient(#000 0.1rem, transparent 0.1rem);background-size:100% 1.2rem}#histList #histHeader{-webkit-user-select:none;-ms-user-select:none;user-select:none}#histList .histItem{cursor:pointer;border-radius:2px}#histList .histItem:hover{color:#c7c7c7}#histList .histItem .hisRes{text-align:right}#histList #roller{text-align:center;user-select:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;position:absolute;bottom:0px;left:0;z-index:15;height:20px;width:100%;background-color:#313131;box-shadow:inset 0px 11px 8px -10px black, inset 0px -11px 8px -10px black}#histList #roller:hover{color:#c7c7c7}.clicked{-webkit-filter:brightness(110%);filter:brightness(110%);text-shadow:0 0 5px white, 0 0 10px gainsboro}

